.canvas,
.overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.canvas {
  background-color: var(--brand-black, #000);
  z-index: 1;
  opacity: 0;
  transition: opacity 0.1s ease-in;
}

.canvas.ready {
  opacity: 1;
}

.overlay {
  background: linear-gradient(0deg, rgba(0, 0, 0, 1) 0%, rgba(0, 0, 0, 0) 100%);
  z-index: 2;
}

.container {
  background-color: var(--brand-black, #000);
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
