.box {
  --shimmer-color: var(--brand-purple);
  align-items: center;
  border: 0.5px solid var(--brand-white, #fff);
  border-radius: 4px;
  box-sizing: border-box;
  justify-content: center;
  display: flex;
  position: relative;
  height: 4rem;
  opacity: 0;
  padding: var(--spacing-2, 0.5rem);
  transition: opacity 0.5s ease-in-out;
  width: 100%;
  @media (max-height: 450px) {
    height: 3rem;
    padding: var(--spacing-2, 0.25rem);
  }
}

.box.blue {
  --shimmer-color: var(--brand-blue);
}

.box.show {
  opacity: 1;
}

.box > img {
  max-height: 100%;
  max-width: 100%;
  object-fit: contain;
}

.box::before {
  content: '';
  position: absolute;
  inset: 0;
  border-radius: 4px;
  padding: 0.5px;
  background: linear-gradient(
    115deg,
    transparent 20%,
    var(--brand-white) 40%,
    var(--shimmer-color) 60%,
    transparent 80%
  );
  background-size: 200% 200%;
  -webkit-mask:
    linear-gradient(var(--brand-white, #fff) 0 0) content-box,
    linear-gradient(var(--brand-white, #fff) 0 0);
  mask:
    linear-gradient(var(--brand-white, #fff) 0 0) content-box,
    linear-gradient(var(--brand-white, #fff) 0 0);
  -webkit-mask-composite: xor;
  mask-composite: exclude;
  opacity: 0;
  transition: opacity 0.3s ease;
  pointer-events: none;
}

.box:hover::before {
  opacity: 1;
  animation: borderShimmer 1.5s linear infinite;
}

@keyframes borderShimmer {
  0% {
    background-position: 0% 50%;
  }
  100% {
    background-position: 100% 50%;
  }
}
